.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.dontMobile {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(4deg, #4063b3, #43dfe4);
}

.dontMobile_text {
  color: white;
  font-size: 30px;
  line-height: 20px;
  padding-top: 10vh;
  text-align: center;
}
